.list {
  list-style: none;
  padding-bottom: 20px;
  padding-top: 20px;
}
.listItem {
  position: relative;
  height: 320px;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
}
.listItem:not(:last-child) {
  margin-bottom: 20px;
}
.imgBack {
  position: absolute;
  display: block;
  z-index: 1;
  min-width: 100%;
  min-height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.link {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  z-index: 999;
}
.date {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  font-size: 14px;
  line-height: 1;
  padding: 10px 20px;
  background: #60B6D9;
  color: #000;
  z-index: 2;
}
.newsHead {
  width: 100%;
  z-index: 2;
  padding: 20px 10px;
  text-transform: uppercase;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  background: rgba(34, 34, 34, .7);
}
.error {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  margin: 70px 0;
  font-size: 36px;
}
