.wrapper {
  padding-top: 50px;
}
.newsHeader {
  text-transform: uppercase;
  font-size: 20px;
  line-height: 1;
  margin-bottom: 30px;
  color: #ffffff;
  font-weight: normal;
}
.button {
  font-size: 20px;
  color: #ffffff;
  line-height: 1;
  padding: 15px 10px;
  display: block;
  width: 100%;
  border: none;
  border-radius: 3px;
  text-decoration: none;
  background: #0B8EC5;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  text-transform: uppercase;
}
.button:hover, .button:active, button:focus {
  text-decoration: none;
  color: #fff;
  outline: none;
}
@media(min-width: 1200px) {
  .wrapper {
    padding-top: 80px;
  }
  .newsHeader {
    font-size: 40px;
  }
  .button {
    font-size: 22px;
    margin: 0 auto;
    width: 430px;
    padding: 17px 10px;
  }
  .button:hover {
    background: #fff;
    color: #0B8EC5;
  }
}
