.titleWrapper {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 10px;
  width: 100%;
}

.innerList {
  list-style: none;
  width: 100%;
  padding: 0 20px;
}

.userInfo {
  position: relative;
  padding-left: 10px;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
}

.userInfo::before {
  position: absolute;
  display: inline-block;
  content: "";
  height: 100%;
  width: 1px;
  background: rgba(255, 255, 255, 0.5);
  top: 0;
  left: 0;
}

.paymentWrapper {
  border-radius: 3px;
  background: rgba(34, 34, 34, 0.5);
  padding: 10px;
}

.debtInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.debtSum {
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  color: #FFFFFF;
}

.debtName {
  font-size: 14px;
  line-height: 24px;
  text-transform: uppercase;
  color: #F96161;
}

.overpayName,
.evenName {
  font-size: 14px;
  line-height: 24px;
  text-transform: uppercase;
  color: #2DFBAA;
}

.paymentLinkDebts {
  display: flex;
  width: 100%;
  border-radius: 3px;
  background: #27AB77;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 5px;
}
.paymentLinkDebts:hover {
  text-decoration: none;
  color: #ffffff;
}
.buttonPay {
  display: block;
  height: 20px;
  width: auto;
  margin-right: 10px;
}

.paymentLinkOverpay, .paymentLinkEven {
  display: flex;
  width: 100%;
  border-radius: 3px;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 5px;
  border: 1px dashed rgba(255, 255, 255, 0.5);
}

.innerListItem {
  padding: 10px 0;
  color: #FFFFFF;
  font-size: 12px;
  line-height: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.innerListItem:not(:last-child) {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.messageLink {
  border-radius: 3px;
  display: block;
  width: 80px;
  background: #0B8EC5;
  text-align: center;
  font-size: 10px;
  line-height: 24px;
  color: #FFFFFF;
  text-transform: capitalize;
  text-decoration: none;
  padding: 3px 0;
}

.messageLink:hover {
  text-decoration: none;
  color: #FFFFFF;
}

@media(min-width: 1200px) {
  .titleWrapper {
    grid-template-columns: 225px 1fr;
    grid-row-gap: 0;
    grid-column-gap: 10px;
    align-items: center;
  }

  .userInfo {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .innerList {
    background: #5795ac;
    padding: 0 20px;
  }
  .debtSum {
    font-weight: normal;
    font-size: 16px;
    line-height: 30px;
  }
  .debtInfo {
    margin-bottom: 2px;
  }
  .paymentWrapper {
    background: none;
    border-radius: 0;
    padding: 0;
  }
  .innerListItem {
    display: grid;
    grid-template-columns: 1fr 1fr 80px;
    grid-column-gap: 10px;
  }
}
