.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px 0;
}
.img {
  display: block;
  width: 300px;
  height: auto;
  margin-bottom: 20px;
}
.textWrapper {
  width: 100%;
}
.socials {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.imgControl {
  display: block;
  width: 40px;
  height: auto;
  fill: #60B6D9;
  transition: all .3s ease;
}
.imgControl:hover {
  fill: #0B8EC5;
}
.line {
  width: 30px;
  height: 1px;
  background: #60B6D9;
}
@media(min-width: 1200px) {
  .wrapper {
    padding: 10px 0;
  }
  .img {
    margin-bottom: 0;
  }
  .textWrapper {
    display: flex;
    align-items: center;
    width: unset;
  }
  .callBack {
    margin-right: 70px;
    color: #222222;
    font-size: 22px;
    line-height: 1;
  }
  .linkControl:not(:last-child) {
    margin-right: 20px;
  }
  .socials {
    width: unset;
  }
}
