.relative {
  position: relative;
  height: 300px;
  overflow: hidden;
}

.relative:not(:last-child){
  margin-bottom: 10px;
}

.relative .img {
  display: block;
  width: 300px;
  height: auto;
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.imgText {
  font-style: italic;
  font-weight: 300;
  font-size: 12px;
  line-height: 18px;
  text-align: center;
  color: #FFFFFF;
}

@media(min-width: 1200px) {
  .grid {
    display: grid;
    grid-template-columns: 750px 1fr;
    grid-column-gap: 50px;
  }

  .dateLinks {
    margin-bottom: 40px;
  }

  .social {
    height: 30px;
  }

  .socialLink:not(:last-child) {
    margin-right: 30px;
  }

  .date {
    font-size: 22px;
    line-height: 30px;
  }

  .articleTitle {
    margin-bottom: 30px;
  }

  .singleArticle h1 {
    font-size: 30px;
    line-height: 38px;
  }

  .singleArticle h1:not(:last-child) {
    margin-bottom: 30px;
  }

  .singleArticle img:not(:last-child) {
    margin-bottom: 20px;
  }

  .singleArticle picture:not(:last-child) img {
    margin-bottom: 20px;
  }

  .singleArticle h2:not(:last-child),
  .singleArticle h3:not(:last-child),
  .singleArticle h4:not(:last-child) {
    margin-bottom: 20px;
  }

  .singleArticle p:not(:last-child),
  .singleArticle span:not(:last-child) {
    margin-bottom: 20px;
  }

  .singleArticle ul:not(:last-child),
  .singleArticle ol:not(:last-child),
  .singleArticle blockquote:not(:last-child),
  .singleArticle a:not(:last-child),
  .singleArticle iframe:not(:last-child) {
    margin-bottom: 30px;
  }

  .singleArticle h2 {
    font-size: 24px;
    line-height: 1.5;
  }

  .singleArticle h3,
  .singleArticle h4 {
    font-size: 24px;
    line-height: 1.5;
  }

  .singleArticle p,
  .singleArticle span {
    font-size: 18px;
    line-height: 30px;
  }

  .singleArticle ul,
  .singleArticle ol {
    font-size: 18px;
    line-height: 30px;
  }

  .singleArticle th:not(:last-child),
  .singleArticle td:not(:last-child) {
    padding-right: 35px;
  }

  .singleArticle table {
    padding-bottom: 20px;
  }

  .singleArticle th {
    font-size: 18px;
    line-height: 30px;
  }

  .singleArticle td {
    font-size: 18px;
    line-height: 36px;
  }

  .singleArticle blockquote {
    font-size: 18px;
    line-height: 30px;
  }

  .singleArticle a {
    font-size: 18px;
    line-height: 30px;
  }

  .relative {
    height: 500px;
  }

  .imgBack {
    height: 500px;
  }
  .imgText {
    font-size: 16px;
    line-height: 20px;
  }
  .relative:not(:last-child){
    margin-bottom: 20px;
  }
}
