.modalMobile .MuiDialog-paperFullScreen {
  background: linear-gradient(89.93deg,  #1D3139  0.19%, #3A4A4E 99.85%);
  font-family: 'PT Sans';
}
.modalMobile .MuiDialogContent-root > .MuiPaper-root {
  background: transparent;
}
.modalMobile .MuiDialogTitle-root {
  padding: 16px 15px !important;
}
.modalMobile .MuiDialogContent-root {
  padding: 8px 15px !important;
}
@media(min-width: 1200px) {
  .modalMobile .MuiDialog-paperFullScreen {
    background: url('../../assets/img/background/background-bubles.png');
    background-image: -webkit-image-set(url('../../assets/img/background/background-bubles.webp') 1x, url('../../assets/img/background/background-bubles.jp2') 1x, url('../../assets/img/background/background-bubles.png') 1x);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    background-position: center;
  }
}
