.background {
  background: #fff;
}
.wrapper {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.img {
  display: block;
  width: 300px;
  height: auto;
}
.controls {
  display: flex;
}
.links {
  padding-right: 50px;
  display: flex;
}
.linkControl:not(:last-child) {
  margin-right: 20px;
}
.linkControl:hover {
  text-decoration: none;
}
.imgControl {
  display: block;
  width: 40px;
  height: auto;
  fill: #60B6D9;
  transition: all .3s ease;
}
.imgControl:hover {
  fill: #0B8EC5;
}
.customBurger {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 5px 0;
  justify-content: space-between;
  width: 49px;
  cursor: pointer;
}
.burgerLine {
  height: 2px;
  background: #444444;
  width: 100%;
  transition: all 0.3s ease;
}
.burgerLine:nth-child(2n) {
  width: 79%;
}
.customBurger:hover > .burgerLine {
  width: 100%;
}
