.quickMenu .MuiPaper-root {
  background: rgba(51, 137, 162, 0.2) !important;
  background-size: 80px 80px !important;
  background-position: 90% 90% !important;
  background-repeat: no-repeat !important;

}
.quickMenu [class~="Mui-expanded"].MuiPaper-root:first-child {
  background-image: url('../../assets/img/background/quickMenuBackground-firstIcon.svg') !important;
}
.quickMenu [class~="Mui-expanded"].MuiPaper-root:nth-child(2) {
  background-image: url('../../assets/img/background/quickMenuBackground-secondIcon.svg') !important;
}
.quickMenu [class~="Mui-expanded"].MuiPaper-root:nth-child(3) {
  background-image: url('../../assets/img/background/quickMenuBackground-thirdIcon.svg') !important;
}
.quickMenu [class~="Mui-expanded"].MuiPaper-root:nth-child(4) {
  background-image: url('../../assets/img/background/quickMenuBackground-fourthIcon.svg') !important;
}
