.wrapper {
  padding: 34px 0 4px 0;
  width: 1170px;
  margin: 0 auto;
  height: 100%;
}
.header {
  font-size: 40px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 400;
  line-height: 1;
  position: relative;
}
.close {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 40px;
  z-index: 998;
  cursor: pointer;
}
.closeLine {
  position: absolute;
  width: 100%;
  height: 2px;
  background: #fff;
  top: 50%;
  left: 0;
  transition: all 0.3s ease;
}
.closeLine:first-child {
  transform: rotate(45deg);
}
.closeLine:last-child {
  transform: rotate(-45deg);
}
.close:hover > .closeLine  {
  transform: rotate(0);
}
