.span {
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #fff;
  color: #000;
  line-height: 1;
  padding: 5px;
  border: 1px solid black;
}
.span:checked, .span:hover, .span:active, .span:focus {
  background: #fff;
  color: #000;
  outline: none;
}

@media(min-width: 1200px) {
  .span {
    font-size: 16px;
    width: 36px;
    height: 36px;
  }
}
