.sideList {
  display: flex;
  flex-direction: column;
  list-style: none;
}

.sideListItem:not(:last-child) {
  margin-bottom: 30px;
}

.sideListItemTitle {
  font-weight: normal;
  font-size: 24px;
  line-height: 36px;
  text-transform: uppercase;
  color: #FFFFFF;
  width: 180px;
  text-align: center;
  border-bottom: 2px solid #60B6D9;
  margin-bottom: 30px;
}

.newsWrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  height: 280px;
}
.imgBack {
  position: absolute;
  display: block;
  z-index: 1;
  min-width: 100%;
  min-height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.date {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  font-size: 22px;
  line-height: 1;
  padding: 15px 30px;
  background: #60B6D9;
  color: #000;
  z-index: 2;
}
.newsBlock {
  width: 100%;
  z-index: 2;
  height: 100%;
  padding: 20px 20px 45px 20px;
  text-transform: uppercase;
  color: #fff;
  font-size: 18px;
  font-weight: normal;
  line-height: 30px;
  background: rgba(34, 34, 34, .7);
  transition: all 0.3s ease;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 174px;
}
.newsHead {
  margin-bottom: 20px;
  text-transform: uppercase;
  white-space: pre-wrap;
  width: 100%;
  overflow: hidden;
}
.content {
  font-size: 12px;
  line-height: 24px;
  opacity: 0;
  transition: all 0.3s ease;
  overflow: hidden;
  width: 100%;
  white-space: pre-wrap;
  overflow: hidden;
}
.link {
  position: absolute;
  right: 30px;
  bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #fff;
  text-decoration: none;
  transition: all 0.7s ease;
}
.link > i {
  color: #222222;
  width: 18px;
  height: auto;
  transform: rotate(180deg);
  margin-right: -6px;
}
.listItem:not(:first-child) .link {
  opacity: 0;
}
.newsWrapper:hover > .newsBlock {
  padding-top: 50px;
  top: 0;
}
.newsWrapper:hover > .newsBlock > .content, .newsWrapper:hover > .newsBlock > .link {
  opacity: 1;
}
.newsWrapper .link {
  right: 20px;
  bottom: 10px;
}
.newsWrapper:hover > .newsHead {
  height: 280px;
}
.error {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  margin: 70px 0;
  font-size: 48px;
}
.linkLink {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
