.wrapperScreen {
   background: linear-gradient(89.93deg,  #1D3139  0.19%, #3A4A4E 99.85%);
}
@media(min-width: 1200px) {
  .wrapperScreen {
    height: 100vh;
    background: url('../../assets/img/background/background-bubles-light.jpg');
    background-image: -webkit-image-set(url('../../assets/img/background/background-bubles-light.webp') 1x, url('../../assets/img/background/background-bubles-light.jp2') 1x, url('../../assets/img/background/background-bubles-light.jpg') 1x);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}
