.selector {
  display: inline-flex;
  list-style: none;
}

.button {
  display: inline-flex;
  font-family: inherit;
  font-size: 16px;
  cursor: pointer;
  border: none;
  padding: 0 5px;
  color: #929C9F;
  background: none;
  text-decoration: underline;
}
.button:not(:last-child) {
  border-right: 2px solid #929C9F;
}

.active {
  composes: button;
  color: #A8E2FB;
  text-decoration: none;
}
.active:focus, .active:hover, .active:checked, .active:active, .active:visited, .active:target {
  color: #A8E2FB;
  text-decoration: none;
}
