.list {
  list-style: none;
  padding-top: 20px;
}
.listItem {
  position: relative;
  overflow: hidden;
  width: 100%;
  text-align: center;
  border-radius: 3px;
  padding: 10px;
}
.listItem:not(:last-child) {
  margin-bottom: 10px;
}
.background {
  position: absolute;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  top: -5px;
  left: -5px;
  z-index: 1;
  background: #3389A2;
  opacity: 0.2;
}
.link {
  filter: blur(0);
  display: flex;
  text-decoration: none;
  cursor: pointer;
  height: 100%;
  width: 100%;
  opacity: 1;
  z-index: 999;
  text-transform: uppercase;
  align-items: center;
}
.link:hover {
  text-decoration: none;
  color: #A8E2FB;
}
.cardImg {
  display: block;
  height: 60px;
  width: auto;
  margin-right: 10px;
}
.cardName {
  font-weight: 300;
  color: #A8E2FB;
  font-size: 20px;
  line-height: 26px;
  text-align: left;
}
@media(min-width: 1200px) {
  .list {
    height: calc(100vh - 210px);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 0;
  }
  .listItem {
    width: 178px;
    height: 272px;
    padding: 0;
  }
  .listItem:not(:last-child) {
    margin-right: 20px;
  }
  .link {
    padding: 80px 10px 10px 10px;
    flex-direction: column;
  }
  .cardImg {
    margin: 0 auto 10px auto;
  }
  .cardName {
    font-size: 18px;
    line-height: 21px;
    text-align: center;
  }
  .background {
    filter: blur(5px);
    background: #0d3037;
    opacity: 0.9;
  }
  .listItem:not(:last-child) {
    margin-bottom: 0;
  }
}
