.wrapper {
  width: 1170px;
  margin: 0 auto;
  padding-left: 65px;
  display: grid;
  grid-template-columns: 1fr 3fr;
  font-family: 'PT Sans';
  height: calc(100% - 90px);
}
.list {
  position: relative;
  list-style: none;
  color: rgba(255, 255, 255, 0.5);
  transition: all .2s ease;
  font-size: 24px;
  line-height: 1;
  height: 100%;
}
.textWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.imgIcon {
  opacity: 0;
  transition: opacity 0.2s ease;
}
.listItem:hover > .textWrapper > .imgIcon {
  opacity: 1;
}
.listItemText {
  transition: color 0.2s ease;
}
.listItem:hover > .textWrapper > .listItemText {
  color: rgba(255, 255, 255, 1);
}
.listItem, .innerListItem {
  padding: 12px 0;
}
.link {
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  transition: color 0.2s ease;
}
.link:hover {
  color: rgba(255, 255, 255, 1);
}
.innerWrapper {
  position: absolute;
  top: -5px;
  left: 100%;
  height: 100%;
  width: 300%;
  z-index: 999;
}
.innerList {
  list-style: none;
  padding-left: 30px;
}
.innerWrapperLots {
  composes: innerWrapper;
  font-size: 18px;
  line-height: 25px;
  top: -10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  height: 100%;
}
