.background {
  background: #fff;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
}
.left {
  display: flex;
  align-items: center;
}
.burger {
  margin-right: 30px;
  width: 30px;
  height: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
}
.burgerLine {
  width: 100%;
  height: 2px;
  background: #444444;
}
.right {
  display: flex;
  align-items: center;
}
.imgSupport {
  display: block;
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: all .3s ease;
}
.spanWrapper {
  margin-right: 30px;
}
.tels {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
  color: #222222;
  overflow: hidden;
}
.telsTitle {
  font-size: 16px;
  line-height: 36px;
  font-weight: normal;
}
.telWrapper {
  display: flex;
  align-items: center;
}
.telLogo {
  display: block;
  margin-right: 10px;
  width: 20px;
  height: 20px;
}
.telText {
  font-size: 16px;
  line-height: 30px;
}
.heightWrapper {
  overflow: hidden;
}
.tel, .tel:hover {
  text-decoration: none;
  color: inherit;
}
