.wrapper {
  background: #fff;
  height: 100%;
  border-radius: 3px 3px 0 0;
  font-family: 'Roboto';
}
.listWrapper {
  overflow: hidden;
}
.form {
  padding: 0 35px 20px 10px;
  width: 100%;
  background: linear-gradient(90deg, #1A4051 0%, #4B97A7 100%);
  position: relative;
  border-radius: 3px 3px 0 0;
}
.searchImg {
  position: absolute;
  right: 10px;
  top: 21px;
  width: 20px;
  height: 20px;
  z-index: 999;
}
.list {
  list-style: none;
}
.streetName {
  font-size: 14px;
  line-height: 36px;
  padding: 0 10px;
  color: #222222;
  background: #C5E3F0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.arrow {
  transition: all 0.3s ease;
}
.arrowRotate {
  composes: arrow;
  transform: rotate(180deg);
}
.crushName {
  font-size: 16px;
  line-height: 36px;
  color: #000;
  text-transform: uppercase;
  text-align: center;
  padding: 0 10px;
  font-weight: bold;
}
.dateWrapper {
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 10px;
}
.hours {
  font-weight: bold;
  color: #0B8EC5;
}
.timeStart {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.timeEnd {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.timeDiagram {
  margin: 0 10px 12px 10px;
  height: 20px;
  background: #DEDFDF;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.timePercent {
  font-size: 14px;
  line-height: 1;
  color: #000;
  z-index: 3;
}
.listWrapper {
  background: #F0F7FA;
}
.streetList {
  padding: 10px;
  list-style: none;
  font-size: 14px;
  line-height: 20px;
  color: #222222;
}
.listMargin {
  height: 10px;
  background: #fff;
}
.noRezults {
  font-size: 16px;
  color: #000;
  text-align: center;
  padding: 20px;
  text-transform: uppercase;
}
@media(min-width: 1200px) {
  .wrapper {
    border-radius: 3px 0 0 3px;
  }
  .form {
    border-radius: 3px 0 0 0;
    padding: 0 65px 20px 30px;
  }
  .searchImg {
    right: 40px;
  }
  .list {
    overflow-y: auto;
  }
  .streetName {
    padding: 0 30px;
  }
  .crushName {
    padding: 0 30px;
    text-align: left;
  }
  .dateWrapper {
    padding: 0 30px;
  }
  .timeDiagram {
    margin: 0 30px 12px 30px;
  }
  .listWrapper {
    margin: 0 30px;
  }
  .streetList {
    padding: 0 15px 15px 10px;
  }
  .listMargin {
    height: 20px;
  }
}
