.form {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.controlWrapper {
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
}
.checkBoxLabel {
  display: flex;
  align-items: center;
  color: #1E3139;
}
.checkBoxLabel input {
  margin-right: 10px;
}
.forgotPassword {
  text-decoration: none;
  color: #5C7C84;
}
.forgotPassword:hover {
  text-decoration: none;
  color: #5C7C84;
}
.button {
  background: #0B8EC5;
  border-radius: 3px;
  font-size: 14px;
  line-height: 1;
  padding: 15px 10px;
  width: 100%;
  text-align: center;
  border: none;
  color: #fff;
  text-transform: capitalize;
  cursor: pointer;
  transition: all .3s ease;
  margin-bottom: 20px;
}
.button:hover {
  background: #1E3139;
}
.linkButton {
  width: 100%;
  display: block;
  font-size: 14px;
  line-height: 1;
  padding: 15px 10px;
  background: #5C7C84;
  color: #fff;
  border-radius: 3px;
  text-transform: capitalize;
  text-align: center;
  text-decoration: none;
}
.linkButton:not(:last-child) {
  margin-bottom: 20px;
}
@media(min-width: 1200px) {
  .form {
    width: 370px;
  }
  .controlWrapper {
    font-size: 16px;
    margin-bottom: 25px;
  }
  .checkBoxLabel input {
    margin-right: 15px;
  }
  .button {
    margin-bottom: 15px;
    font-size: 16px;
  }
  .buttonWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .linkButton {
    width: 180px;
    background: none;
    border: 1px solid #1E3139;
    font-size: 16px;
    transition: all 0.3s ease;
    color: #1E3139;
  }
  .linkButton:hover {
    background: #1E3139;
    color: #fff;
  }
  .linkButton:not(:last-child) {
    margin-bottom: 0;
  }
}
