.wrapperLoggedOffice.wrapperLoggedOfficeReset .MuiPaper-root {
  background: #1F6987 !important;
  background-size: unset !important;
  background-position: unset !important;
  background-repeat: no-repeat !important;
}
.wrapperLoggedOffice.wrapperLoggedOfficeReset [class~="Mui-expanded"].MuiPaper-root:first-child {
  background-image: none !important;
}
.wrapperLoggedOffice.wrapperLoggedOfficeReset [class~="Mui-expanded"].MuiPaper-root:nth-child(2) {
  background-image: none !important;
}
.wrapperLoggedOffice.wrapperLoggedOfficeReset [class~="Mui-expanded"].MuiPaper-root:nth-child(3) {
  background-image: none !important;
}
.wrapperLoggedOffice.wrapperLoggedOfficeReset [class~="Mui-expanded"].MuiPaper-root:nth-child(4) {
  background-image: none !important;
}
@media (min-width: 1200px) {
  .wrapperLoggedOffice.wrapperLoggedOfficeReset .MuiPaper-root {
    background: #2C4E5B !important;
  }
}
