.showMore {
  display: block;
  margin: 0 auto;
  background: #0B8EC5;
  border-radius: 3px;
  font-family: 'Roboto';
  font-weight: normal;
  text-transform: uppercase;
  cursor: pointer;
  color: #FFFFFF;
  width: 100%;
  font-size: 20px;
  line-height: 36px;
  padding: 7px;
  border: none;
  transition: all 0.3s ease;
}
@media(min-width: 1200px) {
  .showMore {
    width: 430px;
    font-size: 22px;
    line-height: 36px;
  }
  .showMore:hover {
    background: #FFFFFF;
    color: #0B8EC5;
  }
}
