.wrapper {
  padding: 50px 0;
  color: #fff;
  font-size: 14px;
  line-height: 1;
}
.topWrapper {
  margin-bottom: 30px;
}
.blockWrapper {
  margin-bottom: 20px;
}
.blockHead {
  font-size: 20px;
  font-weight: normal;
  line-height: 1;
  margin-bottom: 20px;
  text-transform: uppercase;
}
.telWrapper, .infoWrapper {
  display: flex;
  align-items: center;
}
.infoWrapper:not(:last-child), .telWrapper:not(:last-child) {
  margin-bottom: 15px;
}
.telLogo, .iconImg {
  height: auto;
  width: 30px;
  display: block;
  margin-right: 10px;
}
.blockLink, .tel {
  color: #fff;
  text-decoration: none;
}
.line {
  width: 100%;
  height: 1px;
  background: #60B6D9;
}
.logoWrapper {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}
.imgLogo {
  display: block;
  width: 70px;
  height: auto;
  margin-right: 10px;
}
.weText {
  margin-bottom: 15px;
}
.weText span {
  margin-right: 5px;
}
.weLink {
  color: #87C0CE;
  text-decoration: none;
  text-transform: uppercase;
}
.allRights {
  line-height: 24px;
}
@media(min-width: 1200px) {
  .wrapper {
    padding: 30px 0;
    font-size: 16px;
  }
  .topWrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .blockWrapper {
    margin-bottom: 0;
    width: 31%;
  }
  .blockHead {
    font-size: 24px;
  }
  .line {
    margin-top: 20px;
    background: #ffffff;
  }
  .allRightsWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .logoWrapper {
    order: 2;
  }
  .allRights {
    order: 1;
    white-space: pre-line;
  }
}
