.header {
  background: #444444;
  padding: 10px 0;
}
.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.text {
  font-size: 16px;
  line-height: 1;
  display: flex;
  align-items: center;
  color: #fff;
}
.img {
  width: 21px;
  height: 21px;
  display: block;
  margin-right: 5px;
}
.tel, .tel:hover {
  text-decoration: none;
  color: inherit;
}
