.id {
  font-weight: bold;
  color: red;
}

.arrow {
  display: block;
  width: 6px;
  height: auto;
  margin-right: 12px;
  fill: #fff;
  transition: all 0.3s ease;
}

.singleArticle img {
  display: block;
  width: 100%;
  height: auto;
}

.singleArticle img:not(:last-child) {
  margin-bottom: 10px;
}

.singleArticle picture:not(:last-child) img {
  margin-bottom: 10px;
}

.articleTitle {
  margin-bottom: 20px;
}

.singleArticle h1 {
  font-weight: normal;
  font-size: 20px;
  line-height: 36px;
  text-transform: uppercase;
  color: #FFFFFF;
}

.singleArticle h1:not(:last-child) {
  margin-bottom: 20px;
}

.singleArticle h2,
.singleArticle h3,
.singleArticle h4 {
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  color: #60B6D9
}

.singleArticle h2:not(:last-child),
.singleArticle h3:not(:last-child),
.singleArticle h4:not(:last-child) {
  margin-bottom: 10px;
}

.singleArticle h3,
.singleArticle h4 {
  text-transform: none;
}

.singleArticle p,
.singleArticle span {
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
}

.singleArticle p:not(:last-child),
.singleArticle span:not(:last-child) {
  margin-bottom: 10px;
}

.singleArticle ul,
.singleArticle ol {
  font-weight: 300;
  font-size: 14px;
  line-height: 30px;
  color: #FFFFFF;
  padding-left: 15px;
}

.singleArticle ul:not(:last-child),
.singleArticle ol:not(:last-child) {
  margin-bottom: 20px;
}

.singleArticle table {
  width: auto;
  padding-bottom: 10px;
}

.singleArticle th {
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  color: #FFFFFF;
  text-align: left;
  vertical-align: bottom;
}

.singleArticle th::after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background: #60B6D9;
}

.singleArticle th:not(:last-child),
.singleArticle td:not(:last-child) {
  padding-right: 20px;
}

.singleArticle td {
  font-weight: 300;
  font-size: 14px;
  line-height: 30px;
  color: #FFFFFF;
  text-align: left;
}

.singleArticle blockquote {
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
}

.singleArticle blockquote:not(:last-child),
.singleArticle a:not(:last-child), .singleArticle iframe:not(:last-child) {
  margin-bottom: 10px;
}

.singleArticle iframe {
  max-width: 100%;
  overflow-x: auto;
}

.singleArticle a {
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  text-decoration-line: underline;
  color: #60B6D9;
}

.link {
  font-weight: 300;
  font-size: 18px;
  line-height: 18px;
  display: inline-flex;
  align-items: center;
  text-transform: uppercase;
  color: #FFFFFF;
  text-decoration: none;
  transition: all 0.3s ease;
  cursor: pointer;
  transform: translate(0, -20px);
}

.link:hover {
  color: #60B6D9;
}

.link:hover>.arrow {
  fill: #60B6D9;
}

.dateLinks {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.date {
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #60B6D9;
}

.linkWrapper {
  display: flex;
  justify-content: space-between;
}

.socialLink:not(:last-child) {
  margin-right: 20px;
}

.social {
  display: block;
  height: 20px;
  width: auto;
  fill: #60B6D9;
  transition: all 0.3s ease;
}

.socialLink:hover>.social {
  fill: #FFFFFF;
}

@media(min-width: 1200px) {
  .grid {
    display: grid;
    grid-template-columns: 750px 1fr;
    grid-column-gap: 50px;
  }

  .dateLinks {
    margin-bottom: 40px;
  }

  .social {
    height: 30px;
  }

  .socialLink:not(:last-child) {
    margin-right: 30px;
  }

  .date {
    font-size: 22px;
    line-height: 30px;
  }

  .articleTitle {
    margin-bottom: 30px;
  }

  .singleArticle h1 {
    font-size: 30px;
    line-height: 38px;
  }

  .singleArticle h1:not(:last-child) {
    margin-bottom: 30px;
  }

  .singleArticle img:not(:last-child) {
    margin-bottom: 20px;
  }

  .singleArticle picture:not(:last-child) img {
    margin-bottom: 20px;
  }

  .singleArticle h2:not(:last-child),
  .singleArticle h3:not(:last-child),
  .singleArticle h4:not(:last-child) {
    margin-bottom: 20px;
  }

  .singleArticle p:not(:last-child),
  .singleArticle span:not(:last-child) {
    margin-bottom: 20px;
  }

  .singleArticle ul:not(:last-child),
  .singleArticle ol:not(:last-child),
  .singleArticle blockquote:not(:last-child),
  .singleArticle a:not(:last-child),
  .singleArticle iframe:not(:last-child) {
    margin-bottom: 30px;
  }

  .singleArticle h2 {
    font-size: 24px;
    line-height: 1.5;
  }

  .singleArticle h3,
  .singleArticle h4 {
    font-size: 24px;
    line-height: 1.5;
  }

  .singleArticle p,
  .singleArticle span {
    font-size: 18px;
    line-height: 30px;
  }

  .singleArticle ul,
  .singleArticle ol {
    font-size: 18px;
    line-height: 30px;
  }

  .singleArticle th:not(:last-child),
  .singleArticle td:not(:last-child) {
    padding-right: 35px;
  }

  .singleArticle table {
    padding-bottom: 20px;
  }
  .singleArticle th {
    font-size: 18px;
    line-height: 30px;
  }
  .singleArticle td {
    font-size: 18px;
    line-height: 36px;
  }
  .singleArticle blockquote {
    font-size: 18px;
    line-height: 30px;
  }
  .singleArticle a {
    font-size: 18px;
    line-height: 30px;
  }
}
