*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;


  font-family: 'Roboto', 'PT Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1, h2, h3, h4 {
  font-family: 'PT Sans';
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  width: 360px;
  margin: 0 auto;
  padding: 0 20px;
}
.cls-tab {
  width: 100%;
  overflow-x: auto;
}
.cls-tab:not(:last-child) {
  margin-bottom: 10px;
}

@media(min-width: 1200px) {
  .wrapper {
    width: 1170px;
    padding: 0;
  }
}
