.linkBackground {
  background: #60B6D9;
}
.linkWrapper {
  display: flex;
  justify-content: space-around;
}
.link {
  display: block;
  text-decoration: none;
  padding: 10px 0;
}
.linkSelected {
  padding-bottom: 7px;
  border-bottom: 3px solid #fff;
}
.imgLink {
  display: block;
  width: 30px;
  height: 30px;
  fill: rgba(30, 49, 57, .5);
}
.linkSelected .imgLink {
  fill: rgba(255, 255, 255, 1);
  filter: drop-shadow( 0 5px 3px rgba(0, 0, 0, .4));
}
@media(min-width: 1200px) {
  .linkBackground {
    background: rgba(68, 68, 68, .7);
  }

  .linkWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .link {
    text-align: center;
    text-transform: uppercase;
    font-family: 'PT Sans';
    font-size: 22px;
    line-height: 1;
    color: #fff;
    padding: 9px 0;
  }

  .linkSelected {
    background: #3389A2;
    border-bottom: 1px solid #fff;
    padding-bottom: 9px;
  }
}
