.grid {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}
.officeWrapper {
  order: 1;
  margin-bottom: 50px;
}
.quickMenuWrapper {
  order: 2;
  padding-bottom: 50px;
}
.menuItemIconWrapper {
  height: 100%;
}
.menuItemTitle {
  color: #A8E2FB;
  font-size: 24px;
  line-height: 1;
  text-transform: uppercase;
  font-weight: normal;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.menuItemIconWrapper {
  opacity: 1;
  background-size: 80px 80px;
  background-position: 100% 100%;
  background-repeat: no-repeat;
}
.menuItemWrapper:first-child .menuItemIconWrapper {
  background-image: url('../../assets/img/background/quickMenuBackground-firstIcon.svg');
}
.menuItemWrapper:nth-child(2) .menuItemIconWrapper {
  background-image: url('../../assets/img/background/quickMenuBackground-secondIcon.svg');
}
.menuItemWrapper:nth-child(3) .menuItemIconWrapper {
  background-image: url('../../assets/img/background/quickMenuBackground-thirdIcon.svg');
}
.menuItemWrapper:nth-child(4) .menuItemIconWrapper {
  background-image: url('../../assets/img/background/quickMenuBackground-fourthIcon.svg');
}
.arrow {
  fill: #A8E2FB;
  transform: rotate(90deg);
  transition: all .3s ease;
}
[class~="Mui-expanded"] .arrow {
  transform: rotate(-90deg);
}
@media(min-width: 1200px) {
  .grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 20px;
    height: calc(100vh - 210px);
  }
  .officeWrapper {
    order: unset;
    margin-bottom: 0;
    height: 100%;
    overflow-y: auto;
  }
  .quickMenuWrapper {
    order: unset;
    overflow-y: auto;
    padding-bottom: 0;
  }
  .quickMenuWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 20px;
  }
  .menuItemWrapper:first-child {
    background-image: url('../../assets/img/background/quickMenuBackground-first.jpg');
    background-image: -webkit-image-set(url('../../assets/img/background/quickMenuBackground-first.webp') 1x, url('../../assets/img/background/quickMenuBackground-first.jp2') 1x, url('../../assets/img/background/quickMenuBackground-first.jpg') 1x);
  }
  .menuItemWrapper:nth-child(2) {
    background-image: url('../../assets/img/background/quickMenuBackground-second.jpg');
    background-image: -webkit-image-set(url('../../assets/img/background/quickMenuBackground-second.webp') 1x, url('../../assets/img/background/quickMenuBackground-second.jp2') 1x, url('../../assets/img/background/quickMenuBackground-second.jpg') 1x);
  }
  .menuItemWrapper:nth-child(3) {
    background-image: url('../../assets/img/background/quickMenuBackground-third.jpg');
    background-image: -webkit-image-set(url('../../assets/img/background/quickMenuBackground-third.webp') 1x, url('../../assets/img/background/quickMenuBackground-third.jp2') 1x, url('../../assets/img/background/quickMenuBackground-third.jpg') 1x);
  }
  .menuItemWrapper:nth-child(4) {
    background-image: url('../../assets/img/background/quickMenuBackground-fourth.jpg');
    background-image: -webkit-image-set(url('../../assets/img/background/quickMenuBackground-fourth.webp') 1x, url('../../assets/img/background/quickMenuBackground-fourth.jp2') 1x, url('../../assets/img/background/quickMenuBackground-fourth.jpg') 1x);
  }
  .menuItemWrapper {
    padding: 20px 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow-y: auto;
    border-radius: 3px;
    overflow-x: hidden;
    opacity: 0.8;
  }
  .menuItemTitle {
    padding: 0 20px;
  }
}
