.navWrapper {
  display: flex;
  flex-direction: column;
  position: relative;
}
.mobCalendar {
  order: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.order {
  order: 2;
}
.titleMob {
  font-size: 20px;
  line-height: 36px;
  text-transform: uppercase;
  color: #FFFFFF;
}
@media(min-width: 1200px) {
  .navWrapper {
    flex-direction: row;
    justify-content: space-between;
  }

  .linkList {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'PT Sans';
    width: calc(100% - 285px);
    order: unset;
  }

  .link {
    font-weight: normal;
    font-size: 24px;
    line-height: 36px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    text-decoration: none;
    cursor: pointer;
  }

  .selected {
    composes: link;
    color: #fff;
    border-bottom: 2px solid #60B6D9;
    outline: none;
  }
  .calendar {
    order: unset;
  }
  .title {
    position: unset;
    font-weight: normal;
    font-size: 40px;
    line-height: 36px;
    text-transform: uppercase;
    color: #FFFFFF;
  }
}
