.list {
  padding-top: 30px;
  list-style: none;
  display: grid;
  grid-template-areas: "first first second"
                       "first first third";
  grid-template-rows: 280px 280px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 30px;
  grid-column-gap: 30px;
  padding-bottom: 30px;
}
.listItem:first-child {
  grid-area: first;
  height: 590px;
}
.listItem:nth-child(2) {
  grid-area: second;
}
.listItem:nth-child(3) {
  grid-area: third;
}
.listItem {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  height: 280px;
}
.imgBack {
  position: absolute;
  display: block;
  z-index: 1;
  min-width: 100%;
  min-height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.date {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  font-size: 22px;
  line-height: 1;
  padding: 15px 30px;
  background: #60B6D9;
  color: #000;
  z-index: 2;
}
.newsBlock {
  width: 100%;
  z-index: 2;
  height: 100%;
  padding: 20px 20px 45px 20px;
  text-transform: uppercase;
  color: #fff;
  font-size: 18px;
  font-weight: normal;
  line-height: 30px;
  background: rgba(34, 34, 34, .7);
  transition: all 0.3s ease;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 174px;
}
.newsHead {
  margin-bottom: 20px;
  text-transform: uppercase;
  white-space: pre-wrap;
  width: 100%;
  overflow: hidden;
}
.content {
  font-size: 12px;
  line-height: 24px;
  opacity: 0;
  transition: all 0.3s ease;
  overflow: hidden;
  width: 100%;
  white-space: pre-wrap;
  overflow: hidden;
}
.link {
  position: absolute;
  right: 30px;
  bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #fff;
  text-decoration: none;
  transition: all 0.7s ease;
}
.link > i {
  color: #222222;
  width: 18px;
  height: auto;
  transform: rotate(180deg);
  margin-right: -6px;
}
.listItem:not(:first-child) .link {
  opacity: 0;
}
.listItem:not(:first-child) .newsHead {
  margin-bottom: 10px;
}
.listItem:not(:first-child):hover > .newsBlock {
  padding-top: 50px;
  top: 0;
}
.listItem:not(:first-child):hover > .newsBlock > .content, .listItem:not(:first-child):hover > .newsBlock > .link {
  opacity: 1;
}
.listItem:not(:first-child) .link {
  right: 20px;
  bottom: 10px;
}
.listItem:first-child .newsBlock {
  padding: 30px;
  height: auto;
  position: relative;
  top: unset;
  left: unset;
}
.listItem:not(:first-child):hover > .newsHead {
  height: 280px;
}
.listItem:first-child .newsHead {
  font-size: 22px;
}
.listItem:first-child .content {
  font-size: 16px;
  padding-right: 40px;
  opacity: 1;
}
.error {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  margin: 70px 0;
  font-size: 48px;
}
.linkLink {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
