.close {
  width: 27px;
  height: 27px;
  margin-top: 14px;
  margin-bottom: 30px;
  margin-left: 20px;
  position: relative;
  cursor: pointer;
}
.closeLine {
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background: #fff;
  transform: rotate(45deg);
}
.closeLine:last-child {
  transform: rotate(-45deg);
}
.header {
  margin-left: 20px;
  font-size: 20px;
  line-height: 1;
  color: #fff;
  text-transform: uppercase;
}
