.selector {
  display: inline-flex;
  list-style: none;
  flex-direction: column;
}

.button {
  display: inline-flex;
  font-family: inherit;
  font-size: 16px;
  cursor: pointer;
  border: none;
  color: #929C9F;
  background: none;
  text-decoration: underline;
}
.button:not(:last-child) {
  margin-bottom: 10px;
}

.active {
  composes: button;
  color: #0B8EC5;
  text-decoration: none;
}
.active:focus, .active:hover, .active:checked, .active:active, .active:visited, .active:target {
  color: #0B8EC5;
  text-decoration: none;
}
