.wrapper {
  width: 360px;
  margin: 0  auto;
}
.mapControlWrapper {
  width: 100%;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  border-radius: 3px;
  overflow: hidden;
}
.mapWrapper {
  height: 600px;
}
.header {
  font-size: 20px;
  line-height: 1;
  color: #fff;
  text-transform: uppercase;
  width: 100%;
  padding: 0 20px 20px 20px;
  font-weight: normal;
  margin: 0 auto;
}

@media(min-width: 1200px) {
  .wrapper {
    width: 100%;
  }
  .mapControlWrapper {
    width: 1170px;
    margin: 0 auto;
    flex-direction: row;
    height: 690px;
    padding: 0;
  }
  .mapInfoWrapper {
    width: 400px;
  }
  .mapWrapper {
    height: 100%;
    width: 770px;
  }
  .header {
    font-size: 40px;
    width: 1170px;
    padding: 0;
    padding-bottom: 30px;
  }
}
