.officeWrapper {
  padding: 40px 20px;
  background: #9BC7CF;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
}

.loggingHeader {
  font-size: 20px;
  line-height: 36px;
  text-align: center;
  color: #1E3139;
  white-space: pre-line;
  text-transform: uppercase;
  font-weight: normal;
  margin-bottom: 35px;
  background: url('../../assets/img/icons/user-gray-office-back.svg');
  background-repeat: no-repeat;
  background-size: 80px 80px;
  width: 100%;
  padding: 15px 0;
  background-position: 10% center;
}

.officeWrapperLogged {
  composes: officeWrapper;
  padding: 20px 0;
}

.errorWrapper {
  padding: 5px 10px;
  color: #FE2828;
  font-size: 14px;
  line-height: 21px;
  border: 1px solid #FE2828;
  display: flex;
  align-items: center;
  width: 280px;
  margin: 0 auto 20px auto;
}

.errorIcon {
  display: block;
  width: 30px;
  height: auto;
  margin-right: 5px;
}

@media(min-width: 1200px) {
  .officeWrapper {
    height: 100%;
    overflow-y: auto;
    background: url('../../assets/img/background/officeBackgrund.jpg');
    background-image: -webkit-image-set(url('../../assets/img/background/officeBackgrund.webp') 1x, url('../../assets/img/background/officeBackgrund.jp2') 1x, url('../../assets/img/background/officeBackgrund.jpg') 1x);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 20px;
    min-height: 400px;
  }

  .loggingHeader {
    font-size: 28px;
    line-height: 36px;
    background-position: 20% center;
    margin-bottom: 15px;
  }
  .officeWrapperLogged {
    composes: officeWrapper;
    padding: 20px 30px 15px 30px;
  }
  .errorWrapper {
    width: 370px;
  }
}
