.wrapper {
  width: 100%;
}

.upperUserWrapper {
  padding: 0 20px 10px 20px;
}

.userWrapper {
  display: flex;
  margin-bottom: 10px;
}

.userIcon {
  display: block;
  width: 50px;
  height: auto;
  margin-right: 10px;
}

.emptyImg {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.userTextWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px 0;
}

.userName {
  font-size: 16px;
  line-height: 1;
  color: #222222;
}

.userMail {
  font-weight: bold;
  font-size: 16px;
  line-height: 1;
  color: #0B8EC5;
}

.homeLink {
  font-size: 14px;
  color: #222222;
  text-decoration: underline;
  line-height: 20px;
}

.homeLink:hover {
  color: #222222;
}
.exitWrapper {
  padding-top: 10px;
}
.officeExit {
  display: block;
  height: 25px;
  width: auto;
  margin-left: 10px;
  fill: #ffffff;
}

.exit {
  width: 280px;
  margin: 0 auto;
  border-radius: 3px;
  border: none;
  background: #5C7C84;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  font-size: 14px;
  text-transform: capitalize;
  color: #ffffff;
  line-height: 1;
  cursor: pointer;
}
.loader {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media(min-width: 1200px) {
  .wrapper {
    height: 100%;
  }

  .upperUserWrapper {
    display: flex;
    justify-content: space-between;
    padding: 0 0 10px 0;
  }
  .userWrapper {
    margin-bottom: 0;
  }
  .homeLink {
    line-height: 25px;
  }
  .officeExit {
    fill: rgb(34, 34, 34);
  }
  .exitWrapper {
    padding-top: 15px;
    display: flex;
    justify-content: flex-end;
  }
  .exit {
    color: #222222;
    text-decoration: underline;
    background: none;
    width: unset;
    margin: unset;
  }
  .data {
    height: calc(100% - 110px);
    overflow-y: auto;
  }
}
